import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import ReactMarkdown from 'react-markdown';
import rehypeHighlight from "rehype-highlight";
import "highlight.js/styles/a11y-dark.css";
import "./Post.css"

const Post = () => {
  const [markdownContent, setMarkdownContent] = useState('');
  const [data, setData] = useState(null);
  const { undecodedpostname } = useParams(); // URL  
  const postname = decodeURIComponent(undecodedpostname); // URL 

  useEffect(() => {
    //   
    const publicUrl = process.env.PUBLIC_URL;
    const filePath = publicUrl + '/data.json';

    function getFullLink(title) {
      console.log(data);
      for (let i = 0; i < data.items.length; i++) {
        if (data.items[i].title === title) {
          return data.items[i].dir;
        }
      }
      return null; //     null 
    }

    const fetchData = async () => {
      try {
        const response = await fetch(filePath); // public   
        const jsonData = await response.json();
        setData(jsonData);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    //  
    fetchData().then(mdfilesmaker());
    function mdfilesmaker(){
      if(data){
        fetch(getFullLink(postname))
          .then(response => {
                if (!response.ok) {
                  throw new Error('Failed to fetch markdown file');
                }
                document.title = " ";
                return response.text();
          })
          .then(text => {
            // Markdown HTML   
            setMarkdownContent(text);
          })
          .catch(error => {
            console.error('Error fetching markdown file:', error);
          })
      }
    }
  }, []);
  useEffect(()=>{
    function getFullLink(title) {

      for (let i = 0; i < data.items.length; i++) {
        if (data.items[i].title === title) {
          return data.items[i].dir;
        }
      }
      return null; //     null 
    }
    if(data){
      fetch(getFullLink(postname))
        .then(response => {
              if (!response.ok) {
                throw new Error('Failed to fetch markdown file');
              }
              document.title = postname;
              return response.text();
        })
        .then(text => {
          // Markdown HTML   
          setMarkdownContent(text);
        })
        .catch(error => {
          console.error('Error fetching markdown file:', error);
        })
    }
  },[data])

  return (
    <div className="markdown-container">
      <ReactMarkdown rehypePlugins={[rehypeHighlight]}>{markdownContent}</ReactMarkdown>
    </div>
  );
};

export default Post;
